import React, { FunctionComponent, ReactNode, useState } from 'react'
import styled from '@emotion/styled'
import { Helmet } from 'react-helmet'

import Footer from 'components/Common/Footer'
import Header from './Header'
import Sidebar, { IMenuList } from './Sidebar'
import GlobalStyle from 'style/GlobalStyle'

const Container = styled.main`
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
`
const ContentWrapper = styled.div`
  position: relative;
  top: 60px;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
`

/*
Title :     ,      SEO   .
Description :     SNS   ,         .
Viewport :                , Viewport Meta     .
Content Type :  Meta          .
Social Meta Tag : , ,        Meta .
 Open Graph Data  Meta  ,   Meta   .

     .

<!-- Open Graph data -->
<!--  SNS     -->
<meta property="og:title" content="WebSite Title" />
<meta property="og:type" content="article" />
<meta property="og:url" content="<http://www.my-website.com/>" />
<meta property="og:image" content="<http://my-website.com/image.jpg>" />
<meta property="og:description" content="WebSite Description" />
<meta property="og:site_name" content="Site Name, i.e. Moz" />

<!-- Meta Data for Facebook -->
<!--        -->
<meta property="fb:app_id" content="Application ID"/>
<meta property="fb:admins" content="Facebook numeric ID" />

<!-- Meta Data for Twitter -->
<meta name="twitter:card" content="summary" />
<meta name="twitter:site" content="@publisher_handle" />
<meta name="twitter:title" content="Page Title" />
<meta name="twitter:description" content="Page description less than 200 characters" />
<meta name="twitter:creator" content="@author_handle" />
<meta name="twitter:image" content="<http://www.example.com/image.jpg>" />
*/

export interface ITemplateProps {
  title: string
  description: string
  url: string
  image: string
  selectedCategory?: string
  children: ReactNode
}

const Template: FunctionComponent<ITemplateProps> = function ({
  title,
  description,
  url,
  image,
  selectedCategory,
  children,
}) {
  const [isOpenedSidebar, setisOpenedSidebar] = useState(false)

  const openSidebar = () => {
    setisOpenedSidebar(prev => !prev)
  }

  return (
    <Container>
      <Helmet>
        <title>{title}</title>

        <meta name="description" content={description} />
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        <meta httpEquiv="Content-Type" content="text/html;charset=UTF-8" />

        <meta property="og:type" content="website" />
        <meta property="og:title" content={title} />
        <meta property="og:description" content={description} />
        <meta property="og:image" content={image} />
        <meta property="og:url" content={url} />
        <meta property="og:site_name" content={title} />

        <meta name="twitter:card" content="summary" />
        <meta name="twitter:title" content={title} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:image" content={image} />
        <meta name="twitter:site" content="@COYO" />
        <meta name="twitter:creator" content="@COYO" />
        <meta
          name="google-site-verification"
          content="0ZXx4oZoly8ckpIlDvUrFbRLpK_zuI2m9yVSRKVHr3M"
        />
        <meta
          name="naver-site-verification"
          content="c7fcc27767857b2d349ff70dd2e51382563183fe"
        />
        <html lang="ko" />
      </Helmet>
      <GlobalStyle />
      <ContentWrapper>
        {children}
        <Footer />
      </ContentWrapper>
      <Header openSidebar={openSidebar} />
      {isOpenedSidebar && <Sidebar />}
    </Container>
  )
}

export default Template
